<template>
  <div class="hello row">
    
    <div class="col-md-12" >
      <h1>Pick a bus stop</h1> 
      <!--
    <form>
      <div style="width=100px">
        <label for="input-with-list">Input with datalist</label>
        <b-form-input list="input-list" id="input-with-list" v-model="form.stop"></b-form-input>
        <b-form-datalist id="input-list" :options="this.getNames()"></b-form-datalist>
      </div>
      <b-button type="submit" variant="primary">Submit</b-button>
    </form>
  -->
      <form id="main" v-cloak>

        <div class="bar">
            <!-- Create a binding between the searchString model and the text field -->
            <input type="text" v-model="searchString" placeholder="Enter your search terms" />
        </div>
        <ul>
            <!-- Render a li element for every entry in the computed filteredArticles array. -->  
            <li v-for="stop in filteredBusStops">
                <router-link :to="{ name: 'Map' , params: { id: stop.fields.stop_id } }">
                  <p>{{stop.fields.stop_name}}</p>
                </router-link>
            </li>
        </ul>

      </form>

    </div>
  </div>
</template>

<script>
const apiURL = 'https://data.nantesmetropole.fr/api/records/1.0/search/?dataset=244400404_tan-arrets&q=&rows=10000'

export default {
  name: 'HelloWorld',
  props: { // parametres entrants
    msg: String
  },
  data () {
    return {
      form: {
          stop: ''
        },
      data: Array,
      searchString: ""

    }
  },
  created: function () {
	  //this.tri()
    //this.arrets = this.data.records
    //this.getNames()
    
	},
  mounted () {
    this.axios
      .get(apiURL)
      .then( response=> (this.data = response.data.records))//,
    
    
  },
  computed: {
        // A computed property that holds only those articles that match the searchString.
    filteredBusStops: function () {
      var bus_stop_array = this.data,
        searchString = this.searchString;

      if(!searchString){
        return bus_stop_array;
      }

      searchString = searchString.trim().toLowerCase();

      bus_stop_array = bus_stop_array.filter(function(item){
        if(item.fields.stop_name.toLowerCase().indexOf(searchString) !== -1){
          return item;
        }
      })

      // Return an array with the filtered data.
      return bus_stop_array;;
    }
  },
  methods: {
    onSubmit(event) {
      event.preventDefault()
      alert(JSON.stringify(this.form))
      
    },
    compare: function (a, b) {
    	return ('' + a[0]).localeCompare(b[0]);
	  },
    getNames: function(){
      var tab = new Array
      this.data.forEach(element => {
        tab.push(element.fields.stop_name)
        });
    return tab
    }
   
  }
}
</script>

